html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  font-family: Trebuchet MS, sans-serif;
  font-size: 16px;
  line-height: 1;
  background-image: url("../img/bg__pattern.jpg");
  background-repeat: repeat;
}

@font-face {
  font-family: "exo";
  src: url("../fonts/Exo2.0-Thin.woff2") format("woff2"), url("../fonts/Exo2.0-Thin.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

.page__title {
  font-family: 'exo';
  color: #fff;
  font-size: 36px;
  text-align: center;
  margin-top: 2.8em;
}

.cards__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 1120px;
  position: absolute;
  width: 100%;
}

@media only screen and (max-width: 1190px) {
  .cards__wrapper {
    padding: 0 1em;
  }
}

@media only screen and (max-width: 1015px) {
  .cards__wrapper {
    justify-content: space-around;
  }
}

.card:nth-child(2n+2) {
  margin-left: 1em;
  margin-right: 1em;
}

.card {
  padding-top: 44px;
  font-size: 16px;
  min-height: calc(480px - 44px);
  background: transparent;
  position: relative;
  z-index: 1;
  max-width: 320px;
  width: 100%;
  border: none;
  cursor: pointer;
  margin-bottom: 3em;
}

.card:hover .card__weight-info {
  background-color: #2ea8e6;
}

.card:hover .card__content {
  border-left-color: #2ea8e6;
  border-bottom-color: #2ea8e6;
  border-right-color: #2ea8e6;
}

.card:hover .card__corner {
  border-left-color: #2ea8e6;
  border-top-color: #2ea8e6;
}

.card:hover .card__corner-right {
  border-right-color: #2ea8e6;
  border-top-color: #2ea8e6;
}

.card__corner {
  position: absolute;
  height: 44px;
  transform: skew(-45deg);
  top: -4px;
  left: -1px;
  width: calc(100% - 50px);
  border-top: 4px solid #1698d9;
  border-left: calc(4px + 1px) solid #1698d9;
  transform-origin: 0 100%;
  border-bottom: none;
  background-color: #fff;
}

.card__corner-right {
  position: absolute;
  height: 44px;
  width: 50px;
  top: -4px;
  right: 0;
  border-top: 4px solid #1698d9;
  border-right: 4px solid #1698d9;
  border-radius: 0 10px 0 0;
  background-color: #fff;
  overflow: hidden;
}

.card__corner-label {
  position: absolute;
  width: 100%;
  top: 17.6px;
  margin: 0;
  padding: 0 0 0 calc(40px + 5px);
  background-color: transparent;
  font-size: 1em;
  color: #999;
}

.card__content {
  height: 480px;
  overflow: hidden;
  position: relative;
  z-index: -2;
  border-radius: 0 0 10px 10px;
  border: 4px solid #1698d9;
  width: 100%;
  border-top: none;
  box-sizing: border-box;
  top: -4px;
  background-color: #fff;
  margin-bottom: 15px;
}

.card__title {
  font-size: 3em;
  padding-left: 40px;
  font-weight: bold;
  margin-top: 0.15em;
  margin-bottom: 0.1em;
}

.card__subtitle {
  font-size: 1.5em;
  padding-left: 40px;
  font-weight: bold;
  margin-bottom: 0.7em;
}

.card__additionalinfo-wrapper {
  padding-left: 40px;
  font-size: 14px;
}

.card__additionalinfo {
  color: #b9b9b9;
  font-size: 0.9em;
  margin-bottom: 2px;
}

.card__additionalinfo-amount {
  color: #a4a4a4;
  display: inline-block;
  margin-right: 5px;
  font-weight: bold;
}

.card__img-wrapper {
  display: block;
  position: absolute;
  bottom: 0px;
  overflow: hidden;
  height: 330px;
  width: 100%;
  background-color: #fff;
}

.card__image {
  display: block;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, 0);
  width: 130%;
  height: auto;
}

.card__weight-info {
  position: absolute;
  bottom: 0.7em;
  right: 0.7em;
  width: 80px;
  height: 80px;
  background: #1698d9;
  border-radius: 50%;
  color: #fff;
}

.card__weight-amount {
  font-size: 42px;
  text-align: center;
  display: block;
  margin-top: 0.3em;
}

.card__weight-dimension {
  font-size: 21px;
  text-align: center;
  display: block;
}

.card__default-info {
  color: #fff;
  font-size: 13px;
  text-align: center;
}

.card__link {
  color: #1698d9;
  text-decoration: none;
  border-bottom: 1px dashed #1698d9;
  font-size: 13px;
}

.card__selected-info {
  color: #fff;
  font-size: 13px;
  text-align: center;
}

.card__disable-info {
  color: #eded1b;
  font-size: 13px;
  text-align: center;
}

.card--isActive:hover .card__weight-info {
  background-color: #e62e7a;
}

.card--isActive:hover .card__content {
  border-left-color: #e62e7a;
  border-bottom-color: #e62e7a;
  border-right-color: #e62e7a;
}

.card--isActive:hover .card__corner {
  border-left-color: #e62e7a;
  border-top-color: #e62e7a;
}

.card--isActive:hover .card__corner-right {
  border-right-color: #e62e7a;
  border-top-color: #e62e7a;
}

.card--isActive .card__content {
  border-left-color: #d91667;
  border-bottom-color: #d91667;
  border-right-color: #d91667;
}

.card--isActive .card__corner {
  border-left-color: #d91667;
  border-top-color: #d91667;
}

.card--isActive .card__corner-right {
  border-right-color: #d91667;
  border-top-color: #d91667;
}

.card--isActive .card__weight-info {
  background-color: #d91667;
}

.card--disabled {
  cursor: default;
}

.card--disabled .card__content {
  border-left-color: #b3b3b3;
  border-bottom-color: #b3b3b3;
  border-right-color: #b3b3b3;
}

.card--disabled .card__corner {
  border-left-color: #b3b3b3;
  border-top-color: #b3b3b3;
}

.card--disabled .card__corner-right {
  border-right-color: #b3b3b3;
  border-top-color: #b3b3b3;
}

.card--disabled .card__weight-info {
  background-color: #b3b3b3;
}

.card--disabled:hover .card__weight-info {
  background-color: #b3b3b3;
}

.card--disabled:hover .card__content {
  border-left-color: #b3b3b3;
  border-bottom-color: #b3b3b3;
  border-right-color: #b3b3b3;
}

.card--disabled:hover .card__corner {
  border-left-color: #b3b3b3;
  border-top-color: #b3b3b3;
}

.card--disabled:hover .card__corner-right {
  border-right-color: #b3b3b3;
  border-top-color: #b3b3b3;
}

.card--disabled .card__weight-info {
  background-color: #444;
}

.card--disabled:hover .card__weight-info {
  background-color: #444;
}

.card--disabled .card__corner:before,
.card--disabled .card__corner-right:before,
.card--disabled .card__content:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(242, 242, 242, 0.8);
}

.card--hide-info {
  display: none;
}
