body {
	font-family: Trebuchet MS, sans-serif;
	font-size: 16px;
	line-height: 1;
	@include image('bg__pattern.jpg');
	background-repeat: repeat; }

@include font($title, "Exo2.0-Thin");

.page__title {
	font-family: 'exo';
	color: #fff;
	font-size: 36px;
	text-align: center;
	margin-top: 2.8em; }
.cards__wrapper {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	max-width: 1120px;
	position: absolute;
	width: 100%;
	@include r(1190) {
		padding: 0 1em; }
	@include r(1015) {
		justify-content: space-around; } }
.card:nth-child(2n+2) {
	margin-left: 1em;
	margin-right: 1em; }

